import React ,{Component} from 'react'
// import {echarts} from 'echarts'
import './home.css'
// import Calendar from './calendar/calendar'
class Home extends Component{
    constructor(){
        super()
        this.state={
            flag:true
        }
    }
    render(){
        let {flag}=this.state
        return (
            <div className='home'>
                <header>
                    <div></div>
                    <div>
                        <span>
                            账号名：
                            账号id
                        </span>
                    </div>
                </header>
                <section>
                    <nav className='home-nav'>
                        <dl>
                            <dt><i className='icon iconfont'>&#xe72e;</i></dt>
                            <dd>
                                <span>现金账户</span>
                                <b>¥126,560,00</b>
                            </dd>
                        </dl>
                        <dl>
                            <dt><i className='icon iconfont'>&#xe726;</i></dt>
                            <dd>
                                <span>现金账户</span>
                                <b>¥126,560,00</b>
                            </dd>
                        </dl>
                    </nav>
                    <div className='home-cont'>
                        <div className='home-overall'>
                            <h4>整体情况</h4>
                            <ul className='home-Inquire'>
                                <li onClick={()=>{this.cNames()}} className={flag?'active':''}>近7天</li>
                                <li onClick={()=>{this.cNames()}} className={!flag?'active':''}>近30天</li>
                                {/* <li>
                                    <Calendar></Calendar>
                                </li> */}
                            </ul>
                        </div>
                        <div className='home-clickrate'>
                            <p>
                                <span>曝光率（次）</span>
                                <b>278,456</b>
                            </p>
                            <p>
                                <span>曝光率（次）</span>
                                <b>278,456</b>
                            </p>
                            <p>
                                <span>曝光率（次）</span>
                                <b>278,456</b>
                            </p>
                            <p>
                                <span>曝光率（次）</span>
                                <b>278,456</b>
                            </p>
                        </div>
                        <div className='home-echarts' ref='eca'></div>
                    </div>
                </section>
            </div>
        )
    }
    componentDidMount(){
        let echartInstance=echarts.init(this.refs.eca);
        let option = {
            xAxis: {
                type: 'category',
                data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
            },
            yAxis: {
                type: 'value'
            },
            series: [{
                data: [820, 932, 901, 934, 1290, 1330, 1320],
                type: 'line'
            }]
        };
        
        echartInstance.setOption(option)
    }
    cNames(){
        this.setState({
            flag:!this.state.flag
        })
    }
}
export default Home